}
select[disabled] {
  background-color: #ccc;
}
select.invalidate {
  background-color: #fff1f1;
}

input[type="submit"] {
  border: 0;
  cursor: pointer;
}
input[type="checkbox"],
input[type="radio"] {
  width: 14px;
  height: 14px;
  margin-top: -2px;
  vertical-align: middle;
}
input[type="file"],
input[type="text"],
