  border: 1px solid #aaa;
  background-color: #fff;
}
input[type="text"]:focus,
input[type="password"]:focus {
  background-color: #ecf9ff;
}
input[readonly],
input[disabled] {
  background-color: #ccc;
}

button {
  font-family: "Hankyoreh", "Sans-serif";
  border: 0 none;
  padding: 0;
  margin: 0;
  font-size: 100%;
  color: #000;
  vertical-align: middle;
  background: transparent;
  cursor: pointer;
}
button::-moz-focus-inner {
  margin: -1px;
  padding: 0;
}

ul,
ol {
  list-style: none;
}

address,
caption,
cite,
code,
dfn,
var,
em {
  font-style: normal;
  font-weight: normal;
}

textarea {
  width: 100%;
  padding: 5px;
  font-size: 100%;
  border: 1px solid #aaa;
  border-radius: 0;
  resize: none;
}
textarea[disabled],
textarea[readonly] {
  background-color: #ccc;
}

fieldset,
img {
  border: 0;
}

legend {
  width: 0;
  height: 0;
  overflow: hidden;
  margin: 0;
  padding: 0;
  visibility: hidden;
  font-size: 0;
  line-height: 0;
}

/* clear */
.clearfix::after {
  display: block;
  clear: both;
  visibility: hidden;
  height: 0;
  content: ".";
}
* html .clearfix {
  height: 1%;
}
.hidden {
  display: none;
}
.visible {
  position: absolute;
  top: -9999px;
  width: 0;
  height: 0;
  overflow: hidden;
  margin: 0;
  padding: 0;
  visibility: hidden;
  font-size: 0;
  line-height: 0;
}

/* Table */
caption {
  width: 0;
  height: 0;
  overflow: hidden;
  padding: 0;
  visibility: hidden;
  font-size: 0;
  line-height: 0;
}
table {
  width: 100%;
  border-collapse: collapse;
  border-spacing: 0;
  empty-cells: show;
}
table th,
table td {
  vertical-align: middle;
}

